.game-display label{
    font-size: small;
    font-weight: 200;
    letter-spacing: 3px;
}

.game-display hr{
    opacity: 0.25;
    border-top: 1px solid rgb(172, 154, 154);
    margin: 0;
}

.game-display span{
    font-size: smaller;
    font-weight: 200;
}

.game-display {
    top: 0;
    /* display: inline-block; */
    margin: 0px;
    width: 80%;
    min-height: 60px;
    background-color: #26292B;
    color: #fff;
    margin-left: 15px;
    padding: 10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.game-display-bottom{
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}


.separator{
    /* margin-bottom: 5px; */
}


.div-round-button{
    width: 100%;
    padding-top:0px;
    margin-left: 23%;
    margin-bottom: 20px;
}

.round-bottom-div{
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}
