body{
    margin: 0;
    top: 0;
    font-family: sans-serif;
    
}

.activate-chroma{
    background-color: #00ff00;
}

.row{
    width: 100%;
    display: inline-block;
}
.col-1{
    width: 10%;
    float: left;
}
.col-2{
    width: 20%;
    float: left;
}
.col-3{
    width: 30%;
    float: left;
}
.col-4{
    width: 40%;
    float: left;
}
.col-5{
    width: 50%;
    float: left;
}
.col-6{
    width: 60%;
    float: left;
}

.col-10{
    width: 100%;
    float: left;
}




.separator{
    margin-bottom: 20px;
}

/*Round bracket display */
.div-round{
    top: 0;
    display: inline-block;
    width: 250px;
    min-height: 90vh;
    background-color: gray;
    margin-left: 20px;
}

.div-round h3{
    text-align: center;
    font-weight: 300;
    letter-spacing: 4px;
}

.main-bracket-div{
    top: 0;
}
.main-bracket-div h1{
    text-align: center;
}

.bracket-div{
    display: inline-block;
    padding: 0;
}

.mt-60{
    margin-top: 60px;
}

.div-bracket-form{
    margin-left: 20px;
    margin-bottom: 20px;
    background-color: gray;
    padding: 10px;
    width: 850px;
    border-radius: 5px;
}
.form-group {
    float: left;
    margin:5px;
}

.form-group label{
    margin-right: 5px;
    font-weight: 200;
    font-size: small;
}

