
.div-slot-round{
    background-color: gray;
    min-height: 150px;
    min-width: 500px;
    width: 800px;
    margin: 20px;
}

.table-round-input{
    padding: 10px;
}
.table-round-input td{
    padding: 5px;
    padding-right: 15px;
}


.input-slot-round{
    /* width: 100px;
    padding: 2px; */
}

.table-round-input tr td:first-child {
    width: 1%;
    white-space: nowrap;
}

/*Line in second td and third*/
.table-round-input tr td:first-child{
    border-right: thin solid #26292B;
}

.table-round-input tr td:nth-child(2){
    border-right: thin solid #26292B;
}
.table-round-input tr td:last-child{
    padding-left: 20px;
    border-left: thin solid #26292B;
}
.table-round-input tr td{
    text-align: center;
}
.table-round-input input{
    text-align: center;
}
.table-round-input tr {
    border: none;
}
.table-round-input{
    margin-top: 10px;
    border-collapse: collapse;
}


.icon-open-window{
    position: fixed;
    top: 10px;
    left: 10px;
    float: left;
}